.colorsPalette {
  position: absolute;
  bottom: 0;
  width: 100%;
  min-height: 231px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(20px);
  display: flex;
  justify-content: center;
  animation-duration: 0.9s;
  animation-name: slideFromBottom;
  transition-timing-function: ease-out;
}

.colorsPaletteClosing {
  animation-duration: 0.9s;
  animation-name: slideToBottom;
  transition-timing-function: ease-out;
}

@keyframes slideFromBottom {
  from {
    transform: translateY(100dvh);
  }

  to {
    transform: translateY(0dvh);
  }
}

@keyframes slideToBottom {
  from {
    transform: translateY(0dvh);
  }

  to {
    transform: translateY(100dvh);
  }
}

.colorsPaletteEmpty {
  max-width: 214px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.colorsPaletteEmptyTitle {
  color: #000;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-align: center;
  opacity: 0.6;
  margin-top: 11px;
}

.colorsPaletteWrapper {
  width: 100%;
  padding: 12px 0;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.colorsPaletteHead {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 38px;
  padding: 0 15px;
}

.colorsPaletteHeadTitle {
  opacity: 0.6;
}

.colorsPaletteRandom {
  border-radius: 28px;
  background: conic-gradient(
    from 181deg at 50% 50%,
    rgba(255, 0, 0, 0.5) 0deg,
    rgba(250, 255, 0, 0.5) 61.87268257141113deg,
    rgba(20, 255, 0, 0.5) 133.1239664554596deg,
    rgba(0, 224, 255, 0.5) 189.37498569488525deg,
    rgba(36, 0, 255, 0.5) 251.25110149383545deg,
    rgba(255, 0, 199, 0.5) 309.3771457672119deg,
    rgba(255, 0, 0, 0.5) 360deg
  );
  color: #fff;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  outline: 0;
  border: 0;
  transition: 0.3s;
  padding: 7px 11px 6px;
}
.colorsPaletteRandom:hover {
  opacity: 0.7;
}
.colorsPaletteRandom:active {
  opacity: 0.3;
}

.colorsPaletteCreateBtn {
  width: 100%;
  max-width: 335px;
  display: flex;
  padding: 19px 116px 19px 118px;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  border: 1px solid #000;
  background: #000;
  color: #fff;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  outline: 0;
  transition: 0.5s;
}
.colorsPaletteCreateBtn:hover {
  opacity: 0.8;
}
.colorsPaletteCreateBtnDisabled,
.colorsPaletteCreateBtn:active {
  border: 1px solid rgba(0, 0, 0, 0.2);
  opacity: 0.2;
  background: rgba(255, 255, 255, 0.2);
  color: #000;
}
.colorsPaletteCreateBtn:active {
  opacity: 0.5;
}

.colorsPaletteCreateWrapper {
  padding: 0 20px 0;
  display: flex;
  justify-content: center;
}
